<template>
    <div class="container justify-content-md-center">
        <div class="row justify-content-md-center">
            <div class="col-sm-10">
            <h1 class="text-center">Задачи</h1>
            <div style="height: 50px;">
                <confirmation :message="confirmationMessage"
                  v-if="showConfirmation" @x_trigger="show_alert"></confirmation>
            </div>
            <div class="row justify-content-md-center align-items-center">
                <button type="button" id="task-add" class="btn btn-success
                  btn-sm align-left d-block" v-b-modal.todo-modal>Добавить задачу</button>
                <b-alert style="margin: 20px; padding: 3px; height: 33px; width: 400px;"
                  variant="info" show>
                  Выполнено: {{ vTodosDone }} Не выполнено: {{ vTodosNotDone }}</b-alert>
            </div>

            <table class="table table-dark table-stripped table-hover">
                <thead class="thead-light">
                <tr>
                    <th>Uid</th>
                    <th>Описание</th>
                    <th>Выполнена?</th>
                    <th></th>
                </tr>
                </thead>

                <tbody>
                <tr v-for="(todo, index) in todos" :key="index">
                    <td class="todo-uid">{{ todo.uid }}</td>
                    <td>{{ todo.description }}</td>
                    <td>
                    <span v-if="todo.is_completed">Выполнено</span>
                    <span v-else>Не выполнено</span>
                    </td>
                    <td>
                    <div class="btn-group" role="group">
                        <button type="button" class="btn btn-secondary btn-sm"
                                v-b-modal.todo-update-modal @click="updateTodo(todo)">
                            Обновить
                        </button>
                        &nbsp;
                        <button type="button" class="btn btn-danger btn-sm"
                                @click="deleteTodo(todo)">X</button>
                    </div>
                    </td>
                </tr>
                </tbody>

            </table>

            </div>
            <b-modal ref="addTodoModal" id="todo-modal" title="Добавить задачу" hide-footer>
            <b-form @submit="onSubmit" @reset="onReset" class="w-100">
                <b-form-group id="form-description-group"
                                label="Описание:"
                                label-for="form-description-input">
                    <b-form-input id="form-description-input"
                                type="text"
                                v-model="addTodoForm.description"
                                required
                                placeholder="Завести задачу">
                    </b-form-input>
                </b-form-group>
                <b-form-group id="form-complete-group">
                    <b-form-checkbox-group v-model="addTodoForm.is_completed" id="form-checks">
                    <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
                    </b-form-checkbox-group>
                </b-form-group>
                <b-button type="submit" variant="primary">Добавить</b-button>
                <b-button type="reset" variant="danger">Сброс</b-button>
            </b-form>
            </b-modal>

            <b-modal v-if="showModal" ref="updateTodoModal" id="todo-update-modal"
              title="Update" hide-footer>
            <b-form @submit="onUpdateSubmit" @reset="onUpdateReset" class="w-100">
                <b-form-group id="form-update-description-group"
                                label="Описание:"
                                label-for="form-update-description-input">
                    <b-form-input id="form-update-description-input"
                                type="text"
                                v-model="updateTodoForm.description"
                                required>
                    </b-form-input>
                </b-form-group>
                <b-form-group id="form-update-complete-group">
                    <b-form-checkbox-group v-model="updateTodoForm.is_completed"
                        id="form-update-checks">
                    <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
                    </b-form-checkbox-group>
                </b-form-group>
                <b-button-group>
                    <b-button type="submit" variant="primary">Обновить</b-button>
                    <b-button type="reset" variant="danger">Сброс</b-button>
                </b-button-group>
            </b-form>
            </b-modal>
        </div>
    </div>
</template>

<style>
button#task-add {
    margin-top: 20px;
    margin-bottom: 20px;
}
h1, td {
    text-align: left;
}
.todo-uid {
    text-align: right;
}
</style>

<script>
import Confirmation from './Confirmation.vue';

export default {
  name: 'Todo',
  data() {
    return {
      todos: [],
      addTodoForm: {
        description: '',
        is_completed: [],
      },
      updateTodoForm: {
        uid: 0,
        description: '',
        is_completed: [],
      },
      confirmationMessage: '',
      showConfirmation: false,
      showModal: true,
      vTodosDone: 0,
      vTodosNotDone: 0,
    };
  },
  methods: {
    show_alert() {
      this.showConfirmation = !this.showConfirmation;
    },
    getTodos() {
      const temp = JSON.parse(localStorage.getItem('todos'));
      if (temp) {
        this.todos = temp;
        this.vTodosDone = 0;
        this.vTodosNotDone = 0;
        if (temp.length > 0) {
          let i = 0;
          for (i = 0; i < temp.length; i += 1) {
            if (temp[i].is_completed) {
              this.vTodosDone += 1;
            } else {
              this.vTodosNotDone += 1;
            }
          }
        } else {
          this.vTodosDone = 0;
          this.vTodosNotDone = 0;
        }
      }
    },
    resetForm() {
      this.addTodoForm.description = '';
      this.addTodoForm.is_completed = [];
      this.updateTodoForm.description = '';
      this.updateTodoForm.is_completed = [];
    },
    onSubmit(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      let temp = JSON.parse(localStorage.getItem('todos'));
      if (!temp) {
        temp = [];
      }
      const requestData = {
        description: this.addTodoForm.description,
        is_completed: this.addTodoForm.is_completed.length > 0,
      };
      if (temp.length > 0) {
        let i = 0;
        let maxUid = temp[0].uid;
        for (i = 1; i < temp.length; i += 1) {
          if (maxUid < temp[i].uid) {
            maxUid = temp[i].uid;
          }
        }
        requestData.uid = maxUid + 1;
      } else {
        requestData.uid = 1;
      }
      temp.push(requestData);
      const serialObj = JSON.stringify(temp);
      localStorage.setItem('todos', serialObj);
      this.confirmationMessage = `Задача "${requestData.description}" добавлена`;
      this.showConfirmation = true;
      this.resetForm();
      this.getTodos();
    },
    onReset(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      this.resetForm();
    },
    updateTodo(todo) {
      const temp = JSON.parse(localStorage.getItem('todos'));
      let i = 0;
      this.showModal = false;
      for (i = 0; i < temp.length; i += 1) {
        if (temp[i].uid === todo.uid) {
          this.showModal = true;
        }
      }
      if (this.showModal === true) {
        this.updateTodoForm.uid = todo.uid;
        this.updateTodoForm.description = todo.description;
        if (todo.is_completed) {
          this.updateTodoForm.is_completed = [true];
        }
      } else {
        this.getTodos();
        this.confirmationMessage = 'Такой задачи не существует в базе. Список обновлён.';
        this.showConfirmation = true;
      }
    },
    onUpdateSubmit(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      const temp = JSON.parse(localStorage.getItem('todos'));
      const requestData = {
        description: this.updateTodoForm.description,
        is_completed: this.updateTodoForm.is_completed.length > 0,
      };
      requestData.uid = this.updateTodoForm.uid;
      let i = 0;
      for (i = 0; i < temp.length; i += 1) {
        if (temp[i].uid === this.updateTodoForm.uid) {
          temp.splice(i, 1, requestData);
          const serialObj = JSON.stringify(temp);
          localStorage.setItem('todos', serialObj);
        }
      }
      this.getTodos();
      this.confirmationMessage = 'Задача обновлена';
      this.showConfirmation = true;
    },
    onUpdateReset(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      this.resetForm();
    },
    deleteTodo(todo) {
      const temp = JSON.parse(localStorage.getItem('todos'));
      let i = 0;
      let todoExist = false;
      for (i = 0; i < temp.length; i += 1) {
        if (temp[i].uid === todo.uid) {
          temp.splice(i, 1);
          const serialObj = JSON.stringify(temp);
          localStorage.setItem('todos', serialObj);
          todoExist = true;
        }
      }
      if (todoExist) {
        this.confirmationMessage = 'Задача удалена из списка';
      } else {
        this.confirmationMessage = 'Такой задачи не существует в базе. Список обновлён.';
      }
      this.getTodos();
      this.showConfirmation = true;
    },
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
};
</script>
